
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
Popup
},
data () {
return {
couponShowPop: false,  // 默认优惠券弹窗关闭
courseId: '',  // 系列课程id
courseType: '',  // 课程类型 0 套课 1 单课
goodsInfo: '',  // 购买商品信息
userInfo: '',  // 用户信息
couponInfo: '',  // 优惠券信息
couponValue: '',  // 优惠券价格
couponId: '',  // 优惠券id
payStatus: -1,  // 是否购买成功
paySuccessInfo: ''  
}
},
mounted () {
let that = this
that.$nextTick(() => {
that.getBuyDetail()
that.getPaySuccess()
that.setWechat('http://h5.06baobao.com/#/')
})
that.courseId = that.$route.query.courseId
that.courseType = that.$route.query.courseType
},
methods: {

couponPopup (name) {
this.$data[name] = !this.$data[name]
this.couponValue = ''
},
toPage (name) {
this.$router.push({
name: name
})
},

chooseCoupon (name, value, couponid) {
this.couponValue = value
this.couponId = couponid
this.$data[name] = !this.$data[name]
},

getBuyDetail () {
let that = this
let data = {
id: that.courseId,  // 课程id
type: that.courseType,  // 课程类型
token: localStorage.getItem('token')
}
that.axios({
method: 'POST',
url: 'interface/Course/buy_course',
data: data
}).then((res) => {
console.log('购买详情', res)
if (res.err_code !== 1) {
that.showAlert(res.msg)
return false
}
that.goodsInfo = res.data.course
that.userInfo = res.data.user
that.couponInfo = res.data.coupon
})
},

payBtnClick () {
let that = this
let data = {
token: localStorage.getItem('token'),
id: that.courseId,  // 课程id
type: that.courseType,  // 课程类型
order_money: that.goodsInfo.original_free, // 课程价格
c_id: that.couponId  
}
that.axios({
method: 'POST',
url: 'interface/Pay/purchaseColumn',
data: data
}).then((res) => {
console.log('购买', res)
if (Number(res.code) !== 1) {
that.showAlert(res.msg)
return false
}
that.wxpay(res.msg.config)
})
},

wxpay (payData) {
let that = this
let data = {
appId: payData.appId,
nonceStr: payData.nonceStr,
package: payData.package,
paySign: payData.paySign,
signType: payData.signType,
timeStamp: payData.timestamp
}
console.log('支付参数', data)
window.WeixinJSBridge.invoke(
'getBrandWCPayRequest', data,
function (res) {
console.log('调用成功！', res)
if (res.err_msg === 'get_brand_wcpay_request:ok') {

that.payStatus = 1
} else if (res.err_msg === 'get_brand_wcpay_request:cancel') {

that.showAlert('您已取消支付')
} else {
that.$vux.loading.hide()
console.log(res.errMsg)
}
}
)
},

getPaySuccess () {
let that = this
let data = {
token: localStorage.getItem('token'),
type: that.courseType,  // 课程类型
id: that.courseId   
}
that.axios({
method: 'POST',
url: 'interface/Home_page/course_code',
data: data
}).then((res) => {
console.log('购买成功页面', res)
if (res.code !== 1) {
that.showAlert(res.msg)
return false
}
that.paySuccessInfo = res.data
})
}
}
}



